export const shortAddress = (str) => {
    return str.substring(0, 6) + '...' + str.substring(str.length - 6, str.length)
}
export const shortHash = (str) => {
    if(window.innerWidth < 1300) {
        return str.substring(0, 15) + '...' + str.substring(str.length - 15, str.length)
    }
    return str.substring(0, 35) + '...' + str.substring(str.length - 30, str.length)
}