import React, { useState, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import AES from 'crypto-js/aes'
import {enc} from 'crypto-js'
import Header from '../components/Header';
import Footer from '../components/Footer';
import './Home.css';
import { useSolWeb3 } from '../hooks/solweb3';
import { useSendMsg } from '../hooks/useSendMsg'
import axios from 'axios';
import {shortAddress, shortHash} from '../hooks/address'
import { useRandom } from '../hooks/random';

function Home() {
  const [message, setMessage] = useState("");
  const [isLoading, setIsLoading] = useState(true);
  const navigate = useNavigate();

  useEffect(() => {
    setIsLoading(false);
  }, []);

  
  // newaddr history random
  const [switchType, _switchType] = useState('random')
  const [newToAddr, _newToAddr] = useState('')

  const [sending, _sending] = useState(false)

  const { address, provider, onConnect } = useSolWeb3()
  const { onSend } = useSendMsg()

  const [friends, _friends] = useState([])
  const [shadow_friends, _shadow_friends] = useState([])
  const { random_list } = useRandom()

  console.log('random_list----', random_list)
  useEffect(() => {
    if(!address){
      return
    }
    if(switchType === 'history'){
      _friends([])
      // TODO
      axios.get('/api/friend/list', {
        params: {
          address,
        }
      }).then((res) => {
        const data = res.data.data
        console.log('shadow friends data', data)
        if(data){
          const list = JSON.parse(data[0].friends)
          console.log('shadow friends list', list)
          if(list){
            _friends(list)
          }
        }
      })
      return
    }
    if(switchType === 'shadow'){
      _shadow_friends([])
      axios.get('/api/shadow/friend/list', {
        params: {
          address,
        }
      }).then((res) => {
        const data = res.data.data
        console.log('shadow friends data', data)
        if(data){
          const list = JSON.parse(data[0].friends)
          console.log('shadow friends list', list)
          if(list){
            _shadow_friends(list)
          }
        }
      })
      return
    }
  }, [address, switchType])

  const [msglist, _msglist] = useState([])
  const [selectAddr, _selectAddr] = useState('')
  const onSelectAddress = (addr, type) => {
    console.log('addr::', addr)
    _selectAddr(addr)
    if(type === 'random'){
      setMessage('')
      return
    }
    setMessage('')

    axios.post('/api/msg/ab/addr', {
      a: address,
      b: addr,
      limit: 3000,
    }).then((res) => {
      const total = res.data.total
      const data = res.data.data.reverse()
      console.log('msg list', data, total)
      _msglist(data)

      try {
        setTimeout(() => {
          document.getElementsByClassName(data[data.length - 1].hash)[0].scrollIntoView()
        }, 300)
      } catch (error) {
        //
      }
    })
  }
  
  const onShwdowSelectAddress = (addr, type) => {
    console.log('shadow addr::', addr)
    _selectAddr(addr)
    if(type === 'random'){
      setMessage('')
      return
    }
    setMessage('')

    axios.post('/api/shadow/msg/ab/addr', {
      a: address,
      b: addr,
      limit: 300,
    }).then((res) => {
      const total = res.data.total
      const data = res.data.data.reverse()
      console.log('msg list', data, total)
      _msglist(data)

      try {
        setTimeout(() => {
          document.getElementsByClassName(data[data.length - 1].hash)[0].scrollIntoView()
        }, 300)
      } catch (error) {
        //
      }
    })
  }

  console.log('home address::', address)

  const [send_success, _send_success] = useState(false)

  const [all_message, _all_message] = useState([])
    useEffect(() => {
      if(switchType === 'scan'){
        axios.get('/api/msg/list', {
          params: {
            limit: 60,
          }
        }).then((res) => {
          if(res.data.data){
            _all_message(res.data.data.filter(e => e.add_time > 1739282925613))
          }
        })
      }
    }, [switchType, address])

  const onSendClick = async (buttonType) => {
    if(message.length > 600){
      return
    }
    // onSend({ toAddress: '', msg: 'hi'})
    // onSend({ toAddress: '', msg: '??'})
    // onSend({ toAddress: '', msg: message})
    if(switchType === 'newaddr') {
      console.log('newToAddr::', newToAddr)
      _sending(true)
      if(buttonType == 'history') {
        const tx = await onSend({ toAddress: newToAddr, msg: message})
      }
      if(buttonType == 'shadow') {
        const KEY = ['shadow', newToAddr].join('_')
        const c_content = AES.encrypt(message, KEY).toString()
        console.log('shadow message::', c_content)

        
        var bytes  = AES.decrypt(c_content, KEY)
        const msg = bytes.toString(enc.Utf8)
        console.log('msg n s::', msg, newToAddr)
      
        const tx = await onSend({ toAddress: newToAddr, msg: c_content, isShadow: true})
      }
    }
    if(switchType === 'history') {
      console.log('history::', selectAddr)
      _sending(true)
      const tx = await onSend({ toAddress: selectAddr, msg: message})
      _msglist((m) => {
        return [...m, {
          form_addr: address,
          // hash: 'mock-' + new Date(),
          hash: tx,
          to_addr: selectAddr,
          msgtype: 'text',
          text_content: message,
          addTime: +new Date(),
        }]
      })
      setTimeout(() => {
        document.getElementsByClassName(tx)[0].scrollIntoView()
      }, 300)
    }
    console.log('---switchType----::', switchType)
    if(switchType === 'shadow') {
      console.log('shadow history::', selectAddr)
      _sending(true)

      const KEY = ['shadow', selectAddr].join('_')
      
      const c_content = AES.encrypt(message, KEY).toString()

      
      console.log('c_content::', c_content)
      
      const tx = await onSend({ toAddress: selectAddr, msg: c_content, isShadow: true})
      _msglist((m) => {
        return [...m, {
          form_addr: address,
          // hash: 'mock-' + new Date(),
          hash: tx,
          to_addr: selectAddr,
          msgtype: 'text',
          text_content: c_content,
          addTime: +new Date(),
        }]
      })
      setTimeout(() => {
        document.getElementsByClassName(tx)[0].scrollIntoView()
      }, 300)
    }
    
    if(switchType === 'random') {
      console.log('random::', selectAddr)
      _sending(true)
      if(buttonType == 'history') {
        const tx = await onSend({ toAddress: selectAddr, msg: message})
      }
      if(buttonType == 'shadow') {
        const KEY = ['shadow', selectAddr].join('_')
        const c_content = AES.encrypt(message, KEY).toString()      
        const tx = await onSend({ toAddress: selectAddr, msg: c_content, isShadow: true})
      }
      _send_success(true)
      setTimeout(() => {
        _send_success(false)
      }, 3000)
    }
    _sending(false)
    setMessage('')
  }
  return (
    <>
      <div className="loading" style={{ display: isLoading ? 'block' : 'none' }}>
        <span className="loadingfa xuanzhuan">
          <img src="images/loading.png" alt="" />
        </span>
      </div>
      <Header />
      <div className="index">
        <div className="section1">
          <div className="leftChoose">
            <a href="#" onClick={() => _switchType('random')} className={switchType === 'random' ? 'leftChooseYes' : ''}>
              <span className="icon">
                <img src="images/icon3.png" alt="" />
              </span>
              <span className="title">Random on chain chat</span>
            </a>
            <a href="#" onClick={() => {_switchType('newaddr')}} className={switchType === 'newaddr' ? 'leftChooseYes' : ''}>
              <span className="icon">
                <img src="images/icon1.png" alt="" />
              </span>
              <span className="title">Send message to specified address</span>
            </a>
            <a href="#" onClick={() => {
              _switchType('history')
              _selectAddr('')
              _msglist([])
            }} className={switchType === 'history' ? 'leftChooseYes' : ''}>
              <span className="icon">
                <img src="images/icon2.png" alt="" />
              </span>
              <span className="title">History List</span>
            </a>
            <a href="#" onClick={() => {
              _selectAddr('')
              _msglist([])
              _switchType('shadow')
            }} className={switchType === 'shadow' ? 'leftChooseYes' : ''}>
              <span className="icon">
                <img src="images/icon4.png" alt="" />
              </span>
              <span className="title">ShadowLine</span>
            </a>
            <a href="#" onClick={() => _switchType('scan')} className={switchType === 'scan' ? 'leftChooseYes' : ''}>
              <span className="icon">
                <img src="images/icon6.png" alt="" />
              </span>
              <span className="title">On chain scan</span>
            </a>
            <a href="#">
              <span className="icon">
                <img src="images/icon5.png" alt="" />
              </span>
              <span className="title">On chain MEME(coming soon)</span>
            </a>
          </div>
          {switchType === 'newaddr' ? <></> : 
          switchType === 'scan' ? <></> : 
          <div className="centerList">
            {switchType === 'random' ? <div className="title">
              <div className="tit">address</div>
              <button className="changebtn" type="button">
                auto-refresh
              </button>
            </div> : <></>}
            <div className="list">
              {
              switchType === 'history' ? <>
                {friends?.map((e) => {
                return <a className={selectAddr === e ? "current" : ''} href="#" onClick={() => onSelectAddress(e)} key={e}>{shortAddress(e)}</a>
              })}
              </> :
              switchType === 'shadow' ? <>
                {shadow_friends?.map((e) => {
                return <a className={selectAddr === e ? "current" : ''} href="#" onClick={() => onShwdowSelectAddress(e)} key={e}>{shortAddress(e)}</a>
              })}
              </> :
              switchType === 'random' ? <>
                {random_list && random_list?.map((e) => {
                return <a className={selectAddr === e ? "current" : ''} href="#" onClick={() => onSelectAddress(e, 'random')} key={e}>{shortAddress(e)}</a>
              })}
              </> : <>
              </>}
            </div>
          </div>}
          <div className="clear wap" />
          <div className={switchType === 'scan' ? '' : "rightContent"} style={
            switchType === 'newaddr' ? { width: '92%' } : 
            switchType === 'scan' ? { 
              overflow: 'auto',
              height: 'calc(100vh - 150px)' 
            } : 
            {} }>
            {switchType === 'scan' ? <>
              <table style={{ color: '#81FA27', fontSize: '14px', margin: 'auto' }}>
                <thead>
                  <tr>
                    <th>hash</th>
                    <th>message</th>
                    {/* <th>time</th> */}
                    <th>form</th>
                    <th>to</th>
                    <th>scan</th>
                  </tr>
                </thead>
                <tbody>
                  {[...all_message].map((e) => {
                    return <tr key={e.hash}>
                      <td style={{ padding: '2px 30px' }}> 
                        <a href={'https://solscan.io/tx/' + e.hash} style={{ color: '#81FA27' }} target='_blank'>
                          {shortHash(e.hash)}
                        </a>
                      </td>
                      <td style={{ padding: '2px 30px' }}>{e.text_content}</td>
                      {/* <td style={{ padding: '2px 30px' }}>{e.add_time}</td> */}
                      <td style={{ padding: '2px 30px' }}>{shortAddress(e.form_addr)}</td>
                      <td style={{ padding: '2px 30px' }}>{shortAddress(e.to_addr)}</td>
                      <td style={{ padding: '2px 30px' }}> 
                        <a href={'https://solscan.io/tx/' + e.hash} style={{ color: '#81FA27' }} target='_blank'>
                        solscan
                        </a>
                      </td>
                    </tr>
                  })}
                </tbody>
              </table>
            </> : <>
              {false && <div className="link">
                <div className="link1">
                  <a href="#">
                    <img src="images/li1.png" alt="" />
                  </a>
                  <a href="#">
                    <img src="images/li2.png" alt="" />
                  </a>
                </div>
                <div className="link2">
                  <a href="#">
                    <img src="images/ic1.png" alt="" />
                  </a>
                  <a href="#">
                    <img src="images/ic2.png" alt="" />
                  </a>
                  <a href="#">
                    <img src="images/ic3.png" alt="" />
                  </a>
                </div>
              </div>}
              {switchType === 'newaddr' ? <>
                <div style={{ marginTop: '20px' }}></div>
                <input style={{ background: 'transparent', padding: '10px', width: '100%', border: '2px solid #81fa27', color: '#81fa27', borderRadius: '6px' }} 
                placeholder='Send message to specified address' value={newToAddr} onInput={(e) => {
                    _newToAddr(e.target.value)
                  }} />
                  <div style={{ marginBottom: '30px', }}></div>
                </> : <></>}
              {(switchType === 'history' || switchType === 'shadow') && <div style={{ flex: 1, overflow: 'auto', scrollBehavior: 'smooth', padding: '20px 0px' }}>
                {msglist.map((e) => {
                  const isMe =  e.form_addr === address

                  
                  let msg = ''
                  try {
                    const KEY = ['shadow', isMe ? e.to_addr : e.form_addr].join('_')
                    var bytes  = AES.decrypt(e.text_content, KEY)
                    msg = bytes.toString(enc.Utf8)
                  } catch (error) {
                    console.log('error', error)
                  }

                  return <div className={['msg_hash_item', e.hash].join(' ')} key={e.hash} style={{ textAlign: e.form_addr === address ? 'right' : 'left', fontSize: '16px' }}>
                    {/* <div>Time: {Date(e.add_time).toLocaleString()}</div> */}
                    {/* <div>{e.form_addr}</div> */}
                    {isMe ? <>
                      <span>{switchType === 'shadow' ? msg : e.text_content}</span>
                      <span className='msg_send_name'>:{shortAddress(e.form_addr)}</span>
                    </> : <>
                      <span className='msg_send_name'>{shortAddress(address)}:</span>
                      <span>{switchType === 'shadow' ? msg : e.text_content}</span>
                    </>}
                  </div>
                })}
              </div>}
              <div className="textContent" style={(switchType === 'history' || switchType === 'shadow') ? { height: '60px' } : {}}>
                <textarea
                  name=""
                  id=""
                  placeholder="Nice to meet you at NeoCircle!"
                  value={message}
                  onChange={(e) => setMessage(e.target.value)}
                  style={(switchType === 'history' || switchType === 'shadow') ? { height: 'auto', padding: '10px 20px' } : {}}
                />
                <div className="total">
                  <span>{message.length}</span>/600
                </div>
              </div>
              {/* <button className="send" type="button" onClick={() => navigate('/chat')}> */}
              {(switchType === 'history' || switchType === 'shadow') ? 
              <button disabled={sending} className="send" type="button" onClick={() => onSendClick()}>Send</button> :
              <div style={{ display: 'flex', gap: '25px' }}>
                <button disabled={sending} className="send" type="button" onClick={() => onSendClick('history')}>Send Message</button> 
                <button disabled={sending} className="send" type="button" onClick={() => onSendClick('shadow')}>Send ShadowLine</button> 
              </div>
              }
            </>}
          </div>
        </div>
      </div>
      <Footer />
      {send_success && <div style={{ position: 'fixed', zIndex: '9999', top: '30px', left: '50%', marginLeft: '-90px', padding: '10px 20px', color: '#81FA27', border: '1px solid ', fontSize: '16px'}}>
        Message sent successfully  
      </div>}
    </>
  );
}

export default Home; 